#layout-builder-modal button.button--primary:before, 
#layout-builder-modal input[type="submit"].button--primary:before {
  display: none;
}

#layout-builder-modal label {
  color: #ffffff;
  font-size: 1rem;
  font-weight: normal;
  background: none;
  border-radius: 0;
  position: static !important;
  transform: none;
}
#layout-builder-modal label:after {
  display: none;
}
#layout-builder-modal .form-type-color input {
  width: 50px;
  padding: 0;
  border: 0;
  border-radius: 50%;
}

#layout-builder-modal .btn.btn-primary::before {
  display: none;
}

#layout-builder-modal .js-media-library-selection .js-media-library-item {
  position: relative;
}

#layout-builder-modal .ck.ck-editor__main>.ck-editor__editable {
  background: #383A40;
}

#drupal-off-canvas .bs_tab-content .bs_tab-pane .js-form-wrapper .bs-group-title,
#drupal-off-canvas .layout-builder-configure-section summary,
#layout-builder-modal summary,
#drupal-off-canvas summary {
  color: black;
}

#drupal-off-canvas button.button--primary,
#drupal-off-canvas input[type="submit"].button--primary {
  color: white;
  width: 85%;
}

#drupal-off-canvas button.button--primary,
#drupal-off-canvas input[type="submit"].button--primary:hover {
  background: #383A40 !important;
  color: black;
}

#drupal-off-canvas summary svg g,
#drupal-off-canvas summary svg path,
#drupal-off-canvas summary svg {
  fill: inherit;
}

#drupal-off-canvas .bs_input-boxes .fieldset-wrapper>div>div input:checked+label,
#layout-builder-modal .bs_input-boxes .fieldset-wrapper>div>div input:checked+label {
  text-indent: -10px;
  color: #41A5FF;
  font-size: medium;
}

#drupal-off-canvas .bs_input-boxes .fieldset-wrapper>div>div input:checked+label,
#layout-builder-modal .bs_input-boxes .fieldset-wrapper>div>div input+label {
  text-indent: -10px;
  color: white;
  font-size: medium;
}

#layout-builder-modal .description{
  color: #ffffff !important;
}
#block-penchas-primary-admin-actions nav.action-links {
  display: flex;
  gap: 15px;
  padding: 15px;
}
#block-penchas-primary-admin-actions nav.action-links .nav-link:first-child .btn.btn-secondary{
 border:1px solid #011f5b;
}
#block-penchas-primary-admin-actions nav.action-links .nav-link:nth-child(2) .btn.btn-secondary{
  border:1px solid #011f5b;
  background-color: #011f5b;
  color: #fff;
 }
 #block-penchas-primary-admin-actions nav.action-links .nav-link:nth-child(2) .btn.btn-secondary:hover{
  background-color: $yellowLightColor;
  border-color: $yellowLightColor;
  color: #011f5b;
 }
 .page-view-group-media .view-group-media .views-exposed-form .flex-wrap,
 .page-view-group-nodes .view-group-nodes .views-exposed-form .flex-wrap{
  gap: 15px;
 }
 
 #layout-builder-modal tr td{
  background-color: #2c2e32;
 }
 #layout-builder-modal tr:hover td{
  background-color: #2c2e32;
 }
 #layout-builder-modal .tabledrag-toggle-weight-wrapper button.link{
  color: #383a40;
  padding: 12px 14px;
 }
 #layout-builder-modal .tabledrag-toggle-weight-wrapper button.link:hover{
  color: #fff;
 }

 #drupal-off-canvas .layout-builder-remove-block .button--primary.btn {
  color: inherit;
  margin-bottom: 10px;
}
.smartdate--widget .input-group input {
  width: 5.5rem;
}
.smartdate--widget .input-group .input-group-text {
  padding-top: 5px !important;
}
#drupal-off-canvas form.layout-builder-configure-section summary {
  color: inherit;
}
#drupal-off-canvas form.layout-builder-configure-section summary:after{
 border-color: inherit;
}
#drupal-off-canvas form.layout-builder-configure-section button,
#drupal-off-canvas form.layout-builder-configure-section .details-description {
  color: inherit !important;
}
#drupal-off-canvas form.layout-builder-configure-section summary .bs-group-title{
color: inherit !important;
}
#drupal-off-canvas form.layout-builder-configure-section summary .bs-group-title svg path{
 fill:#fff;
}

.media-library-widget-modal{
  font-family: $teritoryFont;
  border-radius: 8px;
  .ui-widget-header{
  color: #011f5b;
  background-color: transparent;
  }
  .ui-dialog-titlebar-close{
    background: url(data:image/svg+xml;base64,) center center no-repeat #383a40;
    background-size: contain;
  }
  .js-media-library-menu{
    display: flex;
        align-items: center;
        list-style: none;
        gap: 10px;
        padding: 0;
        flex-wrap: wrap;
        li{
          a{
            text-decoration: none;
            border: 1px solid #011f5b;
            padding: 5px 10px;
            font-size: 14px;
            color: #011f5b;
            border-radius: 5px;
            &:hover{
              color: #fff;
              background-color: #011f5b;
            }
          }
        }
  }
 #media-library-add-form-wrapper{
  .form-control{
    width: 100%;
    padding: 0.375rem 0.75rem;
    height: auto;
  }

  .js-media-library-add-form{
    .item-list{
      .list-group{
        li{
          padding: 20px;
          [data-drupal-selector="edit-remove-button"]{
            border: 1px solid #011f5b;
            border-radius: 4px;
            padding: 6px 14px;
            text-align: center;
            margin-top: 36px;
            &:hover{
              background: #011f5b;
              color: #fff;
              border-color: #011f5b;
            }
            &::before{
              display: none;
            }
          }
          }

        }
      }
      [data-drupal-selector="edit-actions"]{
      .button{
          margin-right: 20px;
        }
      }
  }
 }
 .views-exposed-form{
  .d-flex{
    align-items: center;
  }
  .form-control{
    width: 100%;
    padding: 0.375rem 0.75rem;
    border-radius: 0.375rem;
    height: auto;
    border-color: #dee2e6;
  }
  [data-drupal-selector="edit-actions"] .btn{
    border: 1px solid #011f5b;
    border-radius: 4px;
    padding: 6px 14px;
    text-align: center;
    margin-top: 36px;
    &:hover{
      background: #ffea3f;
      color: #011f5b;
      border-color: #ffea3f;
    }
    &::before{
      display: none;
    }
  }
 }
 
}